import { Day } from "./Day";
import { ConciertoDays } from "./ValueObjects/ConciertoDays";
import { ConciertoName } from "./ValueObjects/ConciertoName";
export class Concierto {
  constructor(
    private  name: ConciertoName,
    private  days: ConciertoDays, 
    private  place: string,
    private tickets: number,
    private  tickets_saled: number
  ){}

  //Tell dont ask
  public static create(
    name: ConciertoName,
    place: string
  ): Concierto {
    return new Concierto(
      name,
      new ConciertoDays([]),
      place,
      0,
      0
    );
  }

  //command porque muta estado - Side Effects
  //Tell don't ask
  public addDay(newDay: Day): void {
    const current_days = this.days.value();
    current_days.push(newDay);
    this.days = new ConciertoDays(current_days);
  }
  
}